"use client";

import Link from "next/link";
import { useState } from "react";
import { Button } from "@/components/ui/button";
import { Sheet, SheetContent, SheetTrigger } from "@/components/ui/sheet";

export default function NavigationBar() {
  const [isOpen, setIsOpen] = useState(false);

  const navItems = [
    { href: "/", label: "Início" },
    { href: "/listings", label: "Espaços" },
    { href: "/sobre-nos", label: "Sobre Nós" },
    { href: "/terms", label: "Termos" },
  ];

  const [language, setLanguage] = useState("pt");

  const toggleLanguage = () => {
    if (language === "pt") {
      setLanguage("en");
      window.location.href = "/en";
    } else {
      setLanguage("pt");
      window.location.href = "/";
    }
  };

  return (
    <nav className="border-b border-border bg-background/95 backdrop-blur supports-[backdrop-filter]:bg-background/60 sticky top-0 z-50">
      <div className="container mx-auto px-4">
        <div className="flex items-center justify-between h-16">
          {/* Logo */}
          <Link href={language === "pt" ? "/" : "/en"} className="flex items-center space-x-2">
            <div className="font-bold text-2xl text-primary">Ewind</div>
          </Link>

          {/* Desktop Navigation */}
          <div className="hidden md:flex items-center space-x-8">
            {navItems.map((item) => (
              <Link
                key={item.href}
                href={language === "pt" ? item.href : `/en${item.href}`}
                className="text-foreground hover:text-primary transition-colors font-medium"
              >
                {item.label}
              </Link>
            ))}
          </div>

          {/* Desktop Actions */}
          <div className="hidden md:flex items-center space-x-4">
            <Button variant="ghost" asChild>
              <Link href={language === "pt" ? "/host" : "/en/host"}>Anunciar Espaço</Link>
            </Button>
            <Button asChild>
              <Link href={language === "pt" ? "/listings" : "/en/listings"}>Buscar Espaços</Link>
            </Button>
          </div>

          {/* Language Switch Button */}
          <div className="hidden md:block">
            <Button variant="outline" size="sm" onClick={toggleLanguage}>
              {language === "pt" ? "EN" : "PT"}
            </Button>
          </div>

          {/* Mobile Menu */}
          <div className="md:hidden">
            <Sheet open={isOpen} onOpenChange={setIsOpen}>
              <SheetTrigger asChild>
                <Button variant="ghost" size="sm">
                  <div className="flex flex-col space-y-1">
                    <div className="w-5 h-0.5 bg-foreground"></div>
                    <div className="w-5 h-0.5 bg-foreground"></div>
                    <div className="w-5 h-0.5 bg-foreground"></div>
                  </div>
                </Button>
              </SheetTrigger>
              <SheetContent side="right" className="w-[300px]">
                <div className="flex flex-col space-y-6 mt-6">
                  {navItems.map((item) => (
                    <Link
                      key={item.href}
                      href={language === "pt" ? item.href : `/en${item.href}`}
                      className="text-lg font-medium hover:text-primary transition-colors"
                      onClick={() => setIsOpen(false)}
                    >
                      {item.label}
                    </Link>
                  ))}
                  <div className="border-t border-border pt-6 space-y-4">
                    <Button variant="outline" className="w-full" asChild>
                      <Link href={language === "pt" ? "/host" : "/en/host"} onClick={() => setIsOpen(false)}>
                        Anunciar Espaço
                      </Link>
                    </Button>
                    <Button className="w-full" asChild>
                      <Link href={language === "pt" ? "/listings" : "/en/listings"} onClick={() => setIsOpen(false)}>
                        Buscar Espaços
                      </Link>
                    </Button>
                  </div>
                </div>
              </SheetContent>
            </Sheet>
          </div>
        </div>
      </div>
    </nav>
  );
}
